*{
    margin:0;
    padding:0;
  
}

html,body,#root{
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar{
    width: 5px;
    height: 5px;
    background-color: #fff;
  }
  
  
  ::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0);
    background-color: #fff;
  }
  
 
  ::-webkit-scrollbar-thumb{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0);
    background-color: #D5D5D5;
  }

